<template>
  <div class="img-wrap">
    <!-- 加载错误的时候 -->
    <template v-if="isError">
      <span class="loading-tips">
        <img class="error-img" :src="$getImgPrefix('/img/img-error.png')" alt="" />
        <span>图片加载失败</span>
      </span>
    </template>
    <template v-else>
      <img
        class="show-img"
        v-if="imgSrc"
        v-show="!isImgLoading"
        :src="imgSrc"
        alt=""
        @load="imgLoad"
        @error="imgError"
      />
      <span class="loading-tips" :class="{ hide: !isImgLoading }">
        <i class="el-icon-picture"></i>
        <span>图片加载中...</span>
      </span>
    </template>
  </div>
</template>

<script>
  import { getImageSrc } from '@/utils'
  export default {
    components: {},
    props: {
      src: {
        type: String,
        require: true,
      },
    },
    data() {
      return {
        imgSrc: '',
        isImgLoading: true,
        isError: false,
      }
    },
    created() {},
    mounted() {},
    methods: {
      imgLoad() {
        this.isImgLoading = false
      },
      imgError() {
        this.isImgLoading = false
        this.isError = true
      },
    },

    computed: {},

    watch: {
      // 根据token去获取图片的内容
      src: {
        immediate: true,
        handler: function(val) {
          this.isImgLoading = true
          this.isError = false
          // 如果是全链接
          if (val.includes('http')) {
            this.imgSrc = val
          } else {
            getImageSrc(val)
              .then((res) => {
                this.imgSrc = res
              })
              .catch(() => {
                this.isError = true
              })
          }
        },
      },
    },
  }
</script>
<style lang="less" scoped>
  .img-wrap {
    width: 100%;
    height: 100%;
    position: relative;
    .show-img {
      width: 100%;
      height: 100%;
      position: absolute;
      top: 0;
      left: 0;
    }
    .loading-tips {
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      height: 100%;
      width: 100%;
      i {
        font-size: 40px;
        color: #cccccc;
      }
      img.error-img {
        width: 40px !important;
        height: 40px !important;
      }
      span {
        font-size: 12px;
        line-height: 14px;
        transform: scale(0.7);
        color: #666;
        white-space: nowrap;
      }
      .hide {
        display: none;
      }
    }
  }
</style>
