<template>
  <div class="upload-box">
    <div
      class="upload-button"
      v-loading="loading"
      @click="$refs[refName].click()"
    >
      <div v-if='url' class='remove-btn' @click.stop='handleDeleteUrl'>
        <i class="iconfont icon-close"></i>
      </div>
      <img v-if="url" :src="isBase64 ? url : getImageUrl(url)" />
      <i v-else class="iconfont icon-tianjiatupian1"></i>
    </div>
    <input
      :ref="refName"
      type="file"
      name="files"
      style="display: none"
      accept="image/png,image/jpg,image/jpeg,image/bmp"
      @change="fileChange"
    />
  </div>
</template>

<script>
  import API from '@/api/application'

  import ImgWrap from '@/components/ImgWrap'

  import emitter from '@/components/mixins/emitter'
  // func
  import { getImageUrl } from '@/utils'

  export default {
    mixins: [emitter],
    model: {
      prop: 'url',
      event: 'change',
    },
    components: {
      ImgWrap,
    },
    props: {
      // 返回base64
      isBase64: {
        type: Boolean,
      },
      // 默认选择的图片
      url: {
        type: String,
        require: true,
      },
      // 上传链接
      action: {
        type: String,
        default: API.uploadImage,
      },
      // 上传链接
      groupId: {
        type: Number,
        default: -1,
      },
      // 当值改变后是否要触发element-ui的校验事件
      validateEvent: {
        type: Boolean,
        default: true,
      },
      types: {
        type: Array,
        default: () => ['jpg', 'png', 'jpeg', 'bmp'],
      },
      autoUpload: {
        type: Boolean,
        default: true
      }
    },
    data() {
      return {
        getImageUrl,
        refName: 'inputFile',
        loading: false,
        src: '',
      }
    },
    created() {},
    mounted() {},
    methods: {
      fileChange() {
        const [file] = this.$refs[this.refName].files
        this.recieveFile(file)
      },
      // 接收文件上传,可从外部传入文件
      recieveFile(file) {
        const { $refs, refName } = this
        const input = $refs[refName]
        if (!file) {
          return console.log('not has any file')
        }
        if (!this.judgeFile(file)) {
          return
        }
        if (this.isBase64) {
          const reader = new FileReader()
          reader.readAsDataURL(file)
          reader.onload = e => {
            const value = e.target.result
            this.$emit('change', value)
            // 触发element-ui的 trigger 校验
            if (this.validateEvent) {
              this.dispatch('ElFormItem', 'el.form.change', [value])
            }
            input.value = ''
          }
          return
        }
        this.$emit('fileChange', file)
        if (this.autoUpload) {
          this.uploadFile(file)
        }
      },
      uploadFile(file) {
        const { $refs, refName } = this
        const input = $refs[refName]
        const formData = new FormData()
        formData.append('files', file)
        formData.append('groupId', this.groupId)
        this.loading = true
        this.$http({
          url: this.action,
          headers: {
            'Content-Type': 'multipart/form-data',
          },
          customData: formData,
        })
          .then(async (res) => {
            const value = res.data[0]
            this.$emit('change', value)
            // 触发element-ui的 trigger 校验
            if (this.validateEvent) {
              this.dispatch('ElFormItem', 'el.form.change', [value])
            }
          })
          .finally(() => {
            input.value = ''
            this.loading = false
          })
      },
      judgeFile(file) {
        const { types } = this
        const isIn = types.some((type) => file.name.endsWith(type))
        if (!isIn) {
          this.$message.error('图片格式不合法')
          return false
        }
        // 大于1m
        if (file.size > 1 * 1024 * 1024) {
          this.$message.error('图片不能大于1m')
          return false
        }
        return true
      },
      handleDeleteUrl() {
        this.$emit('change', undefined)
        this.$emit('fileChange', undefined)
        const { $refs, refName } = this
        const input = $refs[refName]
        input.value = ''
      }
    },

    computed: {},

    watch: {},
  }
</script>
<style lang="less" scoped>
  .upload-box {
    .upload-button {
      width: 60px;
      height: 60px;
      margin-bottom: 5px;
      text-align: center;
      background: #ffffff;
      border: 1px solid #dddddd;
      border-radius: 3px;
      line-height: 60px;
      position: relative;
      cursor: pointer;
      &:hover {
        .remove-btn {
          display: flex;
        }
      }
      .iconfont {
        font-size: 26px;
        color: #cccccc;
      }
      .remove-btn {
        display: none;
        position: absolute;
        top: -6px;
        right: -6px;
        z-index: 1;
        align-items: center;
        justify-content: center;
        width: 20px;
        height: 20px;
        background: red;
        border-radius: 50%;
        box-shadow: 1px 1px 2px #000000;
        .iconfont {
          font-size: 20px;
          color: #fff;
        }
      }
      img {
        width: 100%;
        height: 100%;
        position: absolute;
        left: 0;
        top: 0;
      }
    }
    /deep/ .el-loading-spinner {
      margin-top: 0;
      left: 50%;
      transform: translate(-50%, -50%);
    }
  }
</style>
