<template>
  <el-dialog
    custom-class="low-code-dialog"
    class='data-select-dialog'
    :title="title"
    :visible="dialogVisible"
    :close-on-click-modal="false"
    :modal-append-to-body="true"
    :append-to-body="true"
    width="800px"
    top="15vh"
    :before-close="handleClose"
  >
    <n-form
      class="create-form"
      ref="dataForm"
      :model="form"
      :rules="rules"
      @submit.native.prevent
      label-width="100px"
      v-loading="loading"
    >
      <n-form-item label="模板名称" prop="name">
        <el-input
          class="form-input"
          v-model="form.name"
          placeholder="请输入模板名称"
        />
      </n-form-item>
      <n-form-item label="模板图标" prop="icon">
        <Upload class="app-icon" ref="upload" v-model="form.icon" />
        <div class="app-icon-desc">
          支持格式为jpg、png、jpeg、bmp，像素120*120，大小不超过1M
        </div>
        <div style="display: flex; align-items: center;">
          <recomment-img @select="$refs.upload.recieveFile($event)" />
          <!-- <el-button style="margin-left: 10px;" @click="$refs.imageSelect.open()">图片库选择</el-button> -->
        </div>
      </n-form-item>
      <n-form-item label="组件预览" prop="thumb">
        <el-image
          v-loading='buildThumbLoading'
          style="width: 100%; height: 100px"
          :src='thumbUrl'
          :preview-src-list='[thumbUrl]'
          fit='contain'
        ></el-image>
        <el-button v-if="showUploadBtn" @click="$refs.upload2.$refs[$refs.upload2.refName].click()">手动上传</el-button>
        <Upload style="display: none;" ref="upload2" v-model="form.thumb" @change="handleThumbChange" />
      </n-form-item>
      <n-form-item label="模板描述" prop="description">
        <el-input
          class="form-textarea"
          placeholder="请输入模板描述"
          v-model="form.description"
          type="textarea"
          :rows="4"
          maxlength="120"
          show-word-limit
        />
      </n-form-item>
      <n-form-item label="包含组件">
        <div v-for='item in versionList' :key='item.elementName' class='version-item'>
          <el-tag class='version-version'>{{item.elementName}}-{{item.elementVersion}}</el-tag>
        </div>
      </n-form-item>
    </n-form>
    <!-- <ImageSelect ref="imageSelect" @confirm="v => form.icon = v" /> -->

    <div slot="footer" class="dialog-footer">
      <el-button
        :loading="loading"
        type="primary"
        @click="handleUploadThumb"
      >确 认
      </el-button>
      <el-button :loading="loading" @click="close">取 消</el-button>
    </div>
  </el-dialog>
</template>

<script>
  import Upload from '@/components/Upload'
  import ImageSelect from '@/components/SourceManage/imageSelect.vue'
  import { getValidators, required } from '@/utils/validateUtils'
  import { getNodeByElementId, treeUtil, getRandomHexadecimal } from '@/utils/tree-utils'
  import _ from 'lodash'
  import API from '@/api/application'
  import { EDIT, FUNCTION_FLAG, JS_FLAG } from '@/config'
  import { getImageUrl } from '@/utils'
  import domtoimage from 'dom-to-image'
  import recommentImg from '@/pages/application/components/recommentImg'
  import { getPropsValues } from '@/renderer/tools/config'
  import TriggerTarget from '@/renderer/tools/trigger'
  import {  HANDLE_COPY, DELETE_LAYOUT_DATA } from '@/config/storeKey'
  import { pageInject } from '@/renderer/tools/pageStore'

  function dataURLToBlob(dataurl) {
    let arr = dataurl.split(','),
      mime = arr[0].match(/:(.*?);/)[1],
      bstr = atob(arr[1]),
      n = bstr.length,
      u8arr = new Uint8Array(n)
    while (n--) {
      u8arr[n] = bstr.charCodeAt(n)
    }
    return new Blob([u8arr], { type: mime })
  }

  export default {
    inject: [...pageInject],
    components: {
      Upload,
      recommentImg,
      ImageSelect
    },
    data() {
      return {
        dialogVisible: false,
        title: '',
        loading: false,
        form: {
          appId: 0,
          name: '',
          icon: '',
          description: '',
          config: undefined,
          id: 0,
          type: '',
          thumb: ''
        },
        thumbDataUrl: '',
        thumbData: null,
        versionList: [],
        rules: {
          name: getValidators([
            {
              message: '请输入模板名称',
              include: required,
              required: true,
              trigger: 'change',
            },
          ]),
          icon: getValidators([
            {
              message: '请选择模板图标',
              include: required,
              required: true,
              trigger: 'change',
            },
          ]),
        },
        showUploadBtn: false,
        buildThumbLoading: false,
        newElementId: ''
      }
    },
    computed: {
      thumbUrl() {
        return this.form.thumb ? getImageUrl(this.form.thumb) : this.thumbDataUrl
      }
    },
    methods: {
      getImageUrl,
      async open({ elementId: copyId, data }) {
        this.title = copyId ? '新增模板' : '修改模板'
        if (copyId) {
          const newElement = await this._storeDispatch(HANDLE_COPY, { elementId: copyId })
          const elementId = newElement.elementId
          this.newElementId = elementId
          const layout = getNodeByElementId(this.getStore().layoutData, elementId)
          this.handleBuildThumb(copyId)
          let events = {}
          let props = {}
          treeUtil.forEach([layout], node => {
            events[node.elementId] = this.getStore().eventsData[node.elementId]
            props[node.elementId] = this.getElementPropsById(node.elementId)
          })
          const configString = JSON.stringify({
            events,
            props
          })

          const functionExp = new RegExp(`${FUNCTION_FLAG}.{12}`, 'g')
          const jsExp = new RegExp(`${JS_FLAG}.{12}`, 'g')
          const usedFunctionIds = configString.match(functionExp) || []

          const functionsData = this.getStore().functionsData.filter((item) =>
            usedFunctionIds.includes(`${FUNCTION_FLAG}${item.id}`)
          )
          const functionsDataString = JSON.stringify(functionsData)
          const usedJsIds = functionsDataString.match(jsExp) || []

          const jsData = this.getStore().jsData.filter((item) =>
            usedJsIds.includes(`${JS_FLAG}${item.id}`)
          )
          const { dynamicVariables: allDynamicVariables, appVariables: allAppVariables } = this.getStore().dynamicProps[this.getStore().layoutData.elementId]
          const allConfigString = JSON.stringify({
            events,
            props,
            functionsData,
            jsData
          })
          const dynamicVariablesExp = /(?<=(\$dynamicVariables\.)).*?(?=(\.value))/g
          const appVariablesExp = /(?<=(\$appVariables\.)).*?(?=(\.value))/g
          const usedDynamicVariableKeys = allConfigString.match(dynamicVariablesExp) || []
          const usedAppVariableKeys = allConfigString.match(appVariablesExp) || []
          const dynamicVariables = {}
          const appVariables = {}
          Object.keys(allDynamicVariables || {}).forEach(key => {
            if (usedDynamicVariableKeys.includes(key)) {
              dynamicVariables[key] = _.cloneDeep(allDynamicVariables[key])
            }
          })
          Object.keys(allAppVariables || {}).forEach(key => {
            if (usedAppVariableKeys.includes(key)) {
              appVariables[key] = _.cloneDeep(allAppVariables[key])
            }
          })

          const config = JSON.stringify({
            layout,
            events,
            props,
            functionsData,
            jsData,
            dynamicVariables,
            appVariables,
            pageId: this.getStore().layoutData.elementId
          })
          this.form.config = config
          this.form.type = layout.elementType
          this.form.appId = +this.$route.query.id
        } else {
          this.form.appId = +this.$route.query.id
          this.form.name = data.name
          this.form.description = data.description
          this.form.icon = data.icon
          this.form.config = data.config
          this.form.id = data.id
          this.form.type = data.type
          this.form.thumb = data.thumb
        }
        const list = treeUtil.toList([JSON.parse(this.form.config).layout])
        const splitString = '__split__'
        this.versionList = _.uniq(list.map(item => `${item.elementType}${splitString}${item.elementVersion}`)).map(item => {
          const [elementType, elementVersion] = item.split(splitString)
          return {
            elementName: getPropsValues.call(this, { elementType, elementVersion }).elementName,
            elementVersion
          }
        })
        this.dialogVisible = true
        this.$nextTick(() => {
          this.$refs.dataForm.clearValidate()
        })
      },
      close() {
        this.clear()
      },
      handleClose(done) {
        this.clear()
        done()
      },
      handleBuildThumb(elementId) {
        this.buildThumbLoading = true
        setTimeout(() => {
          const ref = TriggerTarget.getRef(elementId)
          const targetElement = ref.$el
          domtoimage.toJpeg(targetElement, {bgcolor: '#ffffff', quality: 0.9, style: {margin: '0'}}).then((dataUrl) => {
            this.thumbDataUrl = dataUrl
            const formData = new FormData()
            const file = dataURLToBlob(dataUrl)
            formData.append('files', file)
            this.thumbData = formData
          }).catch((e) => {
            this.showUploadBtn = true
          }).finally(() => {
            this.buildThumbLoading = false
          })
        })
      },
      clear() {
        if (this.newElementId) {
          this._storeDispatch(DELETE_LAYOUT_DATA, { elementId: this.newElementId })
        }
        this.form = {
          name: '',
          icon: '',
          description: '',
          config: undefined,
          id: 0,
          type: '',
          thumb: ''
        }
        this.newElementId = ''
        this.thumbDataUrl = ''
        this.thumbData = null
        this.loading = false
        this.dialogVisible = false
        this.showUploadBtn = false
        this.buildThumbLoading = false
      },
      async handleSubmit() {
        await this.$refs.dataForm.validate()
        this.loading = true
        const data = this.form.id === 0 ? this.form : {...this.form, config: undefined}
        this.$http({
          url: this.form.id === 0 ? API.componentTemplatePrivateSave : API.componentTemplatePrivateUpdate,
          data
        })
          .then(() => {
            this.close()
            this.$emit('success', this.form)
            this.$message.success('保存成功，请在组件模板中选取使用')
          })
          .finally(() => {
            this.$nextTick(() => {
              this.loading = false
            })
          })
      },
      handleUploadThumb() {
        if (!this.thumbUrl) {
          this.$message.error('请手动上传组件预览图')
          return
        }
        if (this.form.thumb) {
          this.handleSubmit()
          return
        }
        this.loading = true
        this.$http({
          url: API.uploadImage,
          headers: {
            'Content-Type': 'multipart/form-data',
          },
          customData: this.thumbData,
        })
          .then((res) => {
            this.form.thumb = res.data[0]
            this.handleSubmit()
          })
          .catch(() => {
            this.$message.error('缩略图上传失败, 请手动选择图片上传')
          })
          .finally(() => {
            this.loading = false
          })
      },
      getElementPropsById(elementId) {
        const realId = elementId === 'app' ? this.getStore().layoutData.elementId : elementId
        return this.getStore().dynamicProps[realId] || {}
      },
      handleThumbChange(url) {
        this.form.thumb = url
        this.showUploadBtn = !url
      }
    }
  }
</script>

<style lang='less' scoped>
  .create-form::v-deep {
    .version-item {
      display: inline-block;
      margin-right: 15px;
    }
  }
</style>
