<template>
  <section class="error-log" :style="logStyle">
    <div
      class="log-container default-theme splitpanes splitpanes--horizontal"
      :class="{ transition: !canDrag }"
      :style="`height: ${expand ? panelHeight : 0}px`"
      v-if="renderer"
    >
      <div class="splitpanes__splitter" @mousedown="touchstart"></div>
      <div class="log-header">
        <div class="left">
          <span class="title">运行日志</span>
          <span class="filter-tag info" @click="desc = !desc"
            >{{ desc ? '倒序' : '正序'
            }}<i
              :class="{
                'el-icon-caret-bottom': !desc,
                'el-icon-caret-top': desc,
              }"
            ></i
          ></span>
          <span
            class="filter-tag error"
            :class="{ active: filterActive === 'error' }"
            @click="toggleFilter('error')"
            >错误({{ logTotal.error }})</span
          >
          <span
            class="filter-tag info"
            :class="{ active: filterActive === 'info' }"
            @click="toggleFilter('info')"
            >信息({{ logTotal.info }})</span
          >
        </div>
        <div class="right">
          <span class="clear" @click="clearLog">清除</span>
        </div>
      </div>
      <div class="log-list">
        <el-collapse class="logs-collapse" v-model="collapseValue">
          <template v-for="log in showLog">
            <!-- 有详情的日志 -->
            <el-collapse-item
              :class="log.type"
              v-if="log.type === 'log' && log.children.length"
              :key="log.time"
              :name="log.time"
            >
              <template slot="title">
                <div class="log-item" :class="log.type" :key="log.time">
                  <el-tag type="info" size="mini">{{ log.time }}</el-tag>
                  <!-- 事件自定义名称 -->
                  <el-tag v-if="log.tag" class="event-name-tag" size="mini">{{
                    log.tag
                  }}</el-tag>
                  <comp-tag
                    v-if="log.target && log.target.elementId"
                    :log="log"
                  ></comp-tag>
                  <template v-if="log.tagList && log.tagList.length">
                    <variable-tag
                      v-for="tag in log.tagList"
                      :key="tag.key"
                      :data="tag"
                    ></variable-tag>
                  </template>
                  <span>{{ log.message }}</span>
                  <span
                    ><span style="color: red"
                      >错误({{ log.errorNum }})</span
                    ></span
                  >
                  <span>信息({{ log.infoNum }})</span>
                  <!-- <el-button
                    type="default"
                    @click.stop="$set(descCache, log.id, !descCache[log.id])"
                    size="mini"
                    >{{ descCache[log.id] ? '倒序' : '正序'
                    }}<i
                      :class="{
                        'el-icon-caret-bottom': !descCache[log.id],
                        'el-icon-caret-top': descCache[log.id],
                      }"
                    ></i
                  ></el-button> -->
                </div>
              </template>
              <div class="log-content">
                <log-tree
                  v-if="collapseValue.includes(log.time)"
                  :data="sortChildren(log.children, descCache[log.id])"
                ></log-tree>
              </div>
            </el-collapse-item>
            <!-- 有详情的错误 -->
            <el-collapse-item
              :class="log.type"
              v-else-if="log.stack"
              :key="log.time"
              :name="log.time"
            >
              <template slot="title"
                >
                <div class="log-item" :class="log.type" :key="log.time">
                  <el-tag type="info" size="mini">{{ log.time }}</el-tag>
                  <comp-tag
                    v-if="log.target && log.target.elementId"
                    :log="log"
                  ></comp-tag>
                  <span>{{ log.message }}</span>
                </div>
              </template>
              <div class="log-content">{{ log.stack }}</div>
            </el-collapse-item>
            <!-- 无详情的日志或错误 -->
            <div
              v-else
              class="log-item outer"
              :class="log.type"
              :key="log.time"
            >
              <el-tag type="info" size="mini">{{ log.time }}</el-tag>
              <el-tag v-if="log.tag" type="success">{{ log.tag }}</el-tag>
              <el-tag class="event-name-tag" v-if="log.eventName" size="mini">{{
                log.eventName
              }}</el-tag>
              <comp-tag
                v-if="log.target && log.target.elementId"
                :log="log"
              ></comp-tag>
              <span>{{ log.message }}</span>
            </div>
          </template>
        </el-collapse>
      </div>
    </div>
    <div class="log-bar">
      <div class="operate">
        <span
          v-for="btn in btns"
          :key="btn.key"
          :class="{ active: active === btn.key }"
          @click="toggleActive(btn.key)"
          ><span
            ><i :class="btn.icon"></i>{{ btn.text
            }}<span class="dot" v-if="showDot(btn.key)"></span
          ></span>
        </span>
      </div>
      <div>
        标签说明 :
        <el-tag type="success">成功类型</el-tag>
        <el-tag class="event-name-tag">事件名称</el-tag>
        <el-tag type="danger">错误类型</el-tag>
        <el-tag>触发对象</el-tag>
        <el-tag class="variable-tag">实时数值</el-tag>
      </div>
    </div>
  </section>
</template>

<script>
import _ from 'lodash'
import compTag from '../compTag'
import logTree from '../logTree'
import 'splitpanes/dist/splitpanes.css'
import variableTag from '../variableTag'
export default {
  name: 'ErrorLog',
  props: {
    logs: {
      type: Array,
      default: () => [],
    },
    logTotal: {
      type: Array,
      default: () => ({ error: 0, info: 0 }),
    },
    top: {
      type: Boolean,
    },
  },
  data() {
    return {
      panelHeight: 300,
      expand: false,
      renderer: false,
      active: '',
      filterActive: '',
      desc: false,
      collapseValue: [],
      btns: [
        {
          icon: 'el-icon-document',
          key: 'log',
          text: '运行日志',
        },
      ],
      descCache: {
      }
    }
  },
  created() {
    window.addEventListener('mouseup', this.touchend)
    this.panelHeight =
      localStorage.getItem('log-panel-height') || this.panelHeight
  },
  destroyed() {
    window.removeEventListener('mouseup', this.touchend)
  },
  components: {
    compTag,
    logTree,
    variableTag
  },
  methods: {
    // 按发生时间排序
    sortChildren(data, desc) {
      const result = _.cloneDeep(data).sort((a, b) => {
        return (new Date(a.time) - new Date(b.time)) * (desc ? -1 : 1)
      })
      return result
    },
    touchstart() {
      if (!this.canDrag) {
        this.canDrag = true
        window.addEventListener('mousemove', this.resizeHeight)
      }
    },
    touchend() {
      this.canDrag = false
      window.removeEventListener('mousemove', this.resizeHeight)
      localStorage.setItem('log-panel-height', this.panelHeight)
    },
    resizeHeight(e) {
      if (!this.canDrag) {
        return
      }
      const top = window.innerHeight - e.clientY - 36
      if (top <= 300) {
        this.panelHeight = 300
      } else if (top >= window.innerHeight - 36 - 50) {
        this.panelHeight = window.innerHeight - 36 - 50
      } else {
        this.panelHeight = top
      }
    },
    toggleFilter(type) {
      if (this.logTotal[type]) {
        this.filterActive = this.filterActive === type ? '' : type
      }
    },
    clearLog() {
      this.filterActive = ''
      this.descCache = {}
      this.$emit('clear')
    },
    toggleState() {
      this.expand = !this.expand
      if (!this.renderer) {
        const loading = this.$loading({
          text: '加载中',
        })
        setTimeout(() => {
          this.renderer = true
          this.$nextTick(() => {
            loading.close()
          })
        }, 50)
      }
    },
    toggleActive(key) {
      this.active = this.active === key ? '' : key
      switch (key) {
        case 'log':
          this.toggleState()
      }
    },
    showDot(key) {
      switch (key) {
        case 'log':
          return this.logs?.length
      }
    },
  },
  computed: {
    logStyle() {
      return this.top ? { zIndex: 10000 } : {}
    },
    showLog() {
      const { filterActive: type, desc } = this
      let result
      if (type) {
        result = this.logs.filter((item) => {
          const itemError = item.errorNum || item.type === 'error'
          return type === 'error' ? itemError : !itemError
        })
      } else {
        result = this.logs
      }
      return _.cloneDeep(result.filter(item => !item.pid)).sort((a, b) => {
        return (new Date(a.time) - new Date(b.time)) * (desc ? -1 : 1)
      })
    },
  },
  watch: {
  }
}
</script>

