<template>
  <div class="variable-tag">
    <n-tooltip
      :value="value.length > limit ? value.slice(0, limit) + '......' : value"
    >
      <el-tag class="value-key" type="warning" size="mini"
        >{{ data.key }}
        <a
          class="download-variable"
          v-if="value.length > limit"
          href="javascript:;"
          @click.stop="download"
          >下载</a
        ></el-tag
      >
    </n-tooltip>
  </div>
</template>

<script>
export default {
  components: {},
  props: {
    data: {
      type: Object,
    },
  },
  data() {
    return {
      limit: 1000,
    }
  },
  created() {},
  mounted() {},
  methods: {
    download() {
      var pom = document.createElement('a')
      pom.setAttribute(
        'href',
        'data:text/plain;charset=utf-8,' + encodeURIComponent(this.value)
      )
      pom.setAttribute('download', this.data.key)
      if (document.createEvent) {
        var event = document.createEvent('MouseEvents')
        event.initEvent('click', true, true)
        pom.dispatchEvent(event)
      } else {
        pom.click()
      }
    },
  },

  computed: {
    value() {
      return this.data.value + ''
    },
  },

  watch: {},
}
</script>
<style lang='less' scoped>
.variable-tag {
  display: inline-block;
  .value-key {
    margin-left: 5px;
    border-color: #e6a23c;
    background-color: #e6a23c;
    color: white;
    .download-variable {
      text-decoration: underline;
      font-size: 12px;
      margin-left: 5px;
    }
  }
}
</style>