import * as API from '@/api/applicationMarket'
import {
  asyncFunc
} from '@/utils'

import {
  jumpPreview
} from '@/utils/url'
import {
  Loading
} from 'element-ui';

const mixins = {
  data() {
    return {
      LoadingFactory: Loading
    }
  },
  methods: {
    // 应用升级
    async appUpgrade(appId) {
      // 获取发布信息
      let loadingInstance = this.LoadingFactory.service()
      const [err, res] = await asyncFunc(API.publishNote, {
        appId
      })
      loadingInstance.close();
      if (err) {
        return Promise.reject()
      }

      const {
        data: {
          appName,
          note,
          version
        },
      } = res

      // 升级确认
      const h = this.$createElement
      await this.$msgbox({
        title: '升级',
        message: h('div', {
          class: 'upgrade-desc-box'
        }, [
          h('div', {
            class: 'upgrade-title'
          }, `${appName} ${version}`),
          h('div', {
            class: 'upgrade-desc'
          }, note),
        ]),
        showCancelButton: true,
        confirmButtonText: '升级',
        cancelButtonText: '取消',
      })
      // 升级操作
      loadingInstance = this.LoadingFactory.service()
      const [, response] = await asyncFunc(API.appUpgrade, {
        appId
      })
      loadingInstance.close();

      if (response) {
        this.$message.success('升级成功')
      }
    },
    useApp(item) {
      const url = jumpPreview({
        appVersionId: item.appId,
        appId: item.appId,
        type: 1
      })
      window.open(url)
    },
    // 应用安装
    async appInstall(item) {
      // 获取发布信息
      let loadingInstance = this.LoadingFactory.service()
      const [publicErr, publicRes] = await asyncFunc(API.publishNote, {
        appId: item.appId
      })
      loadingInstance.close();
      if (publicErr) {
        return Promise.reject()
      }

      const {
        data: {
          appName,
          note
        },
      } = publicRes

      // 安装确认
      const h = this.$createElement
      await this.$msgbox({
        title: '提示',
        message: h('div', {
          class: 'upgrade-desc-box'
        }, [
          h('div', {
            style: {
              fontWeight: 'bold'
            }
          }, `确定要安装应用“${appName}”吗？`),
          h('div', {
            class: 'upgrade-desc'
          }, note),
        ]),
        showCancelButton: true,
        confirmButtonText: '安装',
        cancelButtonText: '取消',
      })
      // 获取发布信息
      loadingInstance = this.LoadingFactory.service()
      const [err, res] = await asyncFunc(API.appInstall, {
        appId: item.appId
      })
      loadingInstance.close();
      if (res) {
        this.$message.success('安装成功')
      }

    },
    // 应用卸载
    async appUninstall(item) {
      const isShortcut = item.type === 'SHORTCUT'
      const operateTitle = isShortcut ? '删除' : '卸载'
      await this.$confirm(`确定要${operateTitle}${isShortcut ? '快捷方式' : '应用'}“${item.appName}”吗？`, `${operateTitle}确认`)
      // 卸载确认
      let loadingInstance = this.LoadingFactory.service()
      const method = isShortcut ? API.appShortcutRemove : API.appUninstall
      const params = isShortcut ? {
        id: item.shortcutId
      } : {
        appId: item.appId
      }
      const [err, res] = await asyncFunc(method, params)
      loadingInstance.close();
      if (res) {
        this.$message.success(`${operateTitle}成功`)
        return Promise.resolve()
      }
      return Promise.reject()
    },
  }
}

export default mixins