<template>
  <el-dialog
    custom-class="low-code-dialog"
    class="source-manage"
    title="资源管理"
    :visible="dialogVisible"
    :close-on-click-modal="false"
    :modal-append-to-body="true"
    :append-to-body="true"
    width="1200px"
    top="5vh"
    :before-close="handleClose"
    v-element-screen
  >
    <div class="dialog-content">
      <n-tabs class="tabs" tab-position="top" v-model="sourceType">
        <n-tab-pane lazy label="图片管理" name="image">
          <div class="pane-header">
            <template v-if="isShowManage">
              <el-button
                type="danger"
                :disabled="!selectedImageList.length"
                @click="handleDeleteBatch"
                >批量删除（{{ selectedImageList.length }}）</el-button
              >
              <el-dropdown @command="handleMoveToGroup">
                <el-button
                  style="margin: 0 10px"
                  :disabled="!selectedImageList.length"
                  >批量分组（{{ selectedImageList.length }}）<i
                    class="el-icon-arrow-down el-icon--right"
                  ></i
                ></el-button>
                <el-dropdown-menu class="top-dropdown" slot="dropdown">
                  <el-dropdown-item
                    v-for="group in imageGroupList"
                    :key="group.id"
                    :command="group.id"
                    :disabled="group.id === defaultSelectId"
                    >{{ group.name }}</el-dropdown-item
                  >
                </el-dropdown-menu>
              </el-dropdown>
            </template>
            <span v-else class="desc">每次上传最多20张图片</span>
            <el-button
              type="primary"
              @click="handleToggleManage"
              style="margin-right: 10px"
              >{{ isShowManage ? '取消' : '管理' }}</el-button>
            <el-upload
              ref="imageUpload"
              :action="imageUploadAction"
              :headers="headers"
              :data="{
                groupId: defaultSelectId,
              }"
              name="files"
              accept="image/png,image/jpg,image/jpeg,image/bmp"
              :show-file-list="false"
              with-credentials
              multiple
              :limit="20"
              :disabled="!defaultSelectId"
              :auto-upload="true"
              :on-success="handleUploadSuccess"
              :on-exceed="onExceed"
              :before-upload="judgeFile"
            >
              <el-button type="primary">图片上传</el-button>
            </el-upload>
          </div>
          <div class="pane-body">
            <div class="group-list">
              <n-tree
                ref="nodeTree"
                class="api-group-tree"
                :data="imageGroupList"
                node-key="id"
                :expand-on-click-node="true"
                default-expand-all
                highlight-current
                :draggable="false"
                icon-class="icon-right"
                :props="{ children: 'children' }"
              >
                <div
                  class="tree-node"
                  :class="{
                    'is-selected': isSelected(data),
                  }"
                  slot-scope="{ data }"
                  @click.stop="handleSelect(data)"
                >
                  <div class="node-name">
                    <div v-if="data.rename">
                      <el-input
                        v-model="data.name"
                        @blur="handleRename(data, data.name)"
                        @keyup.enter.native="handleRename(data, data.name)"
                        placeholder="请输入分组名"
                      ></el-input>
                    </div>
                    <span v-else>{{ data.name }}</span>
                  </div>
                  <el-dropdown
                    v-if="!data.isDefault"
                    class="node-right-menu-icon"
                    trigger="click"
                    @command="(v) => handleImageGroup(v, data)"
                  >
                    <span class="el-dropdown-link" @click.stop>
                      <i class="el-icon-more el-icon--right"></i>
                    </span>
                    <el-dropdown-menu class="top-dropdown" slot="dropdown">
                      <el-dropdown-item command="edit">重命名</el-dropdown-item>
                      <el-dropdown-item command="delete">删除</el-dropdown-item>
                    </el-dropdown-menu>
                  </el-dropdown>
                </div>
              </n-tree>
              <div class="add-group" @click="handleAddGroup">
                <div class="add-btn-primary">+</div>
                <span>新增分组</span>
              </div>
            </div>
            <div class="panel-body-right">
              <template v-if="imageList.length">
                <div
                  class="image-item"
                  v-for="img in imageList"
                  :key="img"
                  :title="img.originalName"
                  @click="handleSelectImage(img, $event)"
                >
                  <template v-if="!isShowManage">
                    <el-image
                      class="image"
                      :src="getImageUrl(img.name)"
                      lazy
                      fit="cover"
                      :preview-src-list="[getImageUrl(img.name)]"
                    ></el-image>
                    <div class="name">{{ img.originalName }}</div>
                  </template>
                  <template v-else>
                    <img class="image" :src="getImageUrl(img.name)" />
                    <div class="name-wrap">
                      <el-checkbox
                        style="pointer-events: none;"
                        :value="
                          selectedImageList.some((item) => item.id === img.id)
                        "
                      ></el-checkbox>
                      <div class="name">{{ img.originalName }}</div>
                    </div>
                  </template>
                </div>
              </template>
              <n-empty v-else description="暂无数据"></n-empty>
            </div>
          </div>
        </n-tab-pane>
        <n-tab-pane lazy label="图标管理" name="iconfont">
          <div class="pane-header">
            <span class="desc"
              >支持上传阿里云图标库导出的zip包，每次上传会替换之前上传的图标</span
            >
            <el-upload
              :action="iconfontUploadAction"
              :headers="headers"
              name="file"
              accept=".zip"
              :show-file-list="false"
              with-credentials
              :multiple="false"
              :auto-upload="true"
              :on-success="handleUploadIconfontSuccess"
              :before-upload="handleBeforeUploadIconfont"
            >
              <el-button type="primary">图标上传</el-button>
            </el-upload>
          </div>
          <div class="pane-body-icon">
            <n-empty
              v-if="!(iconfontData && iconfontData.glyphs.length)"
              description="暂无数据"
            ></n-empty>
            <div v-else class="iconfont-list">
              <div class="iconfont-item" v-for="icon in iconfontData.glyphs" :key="icon.icon_id">
                <div class="iconfont-twrap">
                  <i class="icon" :class="`${iconfontData.font_family} ${iconfontData.css_prefix_text}${icon.font_class}`"></i>
                </div>
                <span class="iconfont-name" :title="`${icon.name}`">
                  {{`${icon.name}`}}
                </span>
                <span
                  class="iconfont-code"
                  :title="`${iconfontData.css_prefix_text}${icon.font_class}`"
                >{{`${iconfontData.css_prefix_text}${icon.font_class}`}}</span>
                <span
                  class="iconfont-code iconfont-code-show"
                  :title="`${iconfontData.css_prefix_text}${icon.font_class}`"
                >{{`${iconfontData.css_prefix_text}${icon.font_class}`}}</span>
                <div class="iconfont-cover iconfont-cover-unfreeze" title="点击复制图标代码" @click="handleCopy(`${iconfontData.font_family} ${iconfontData.css_prefix_text}${icon.font_class}`)">
                  <!-- <div class="cover-code J_cover_code">
                    <span class="copy-before"><i class="el-icon-document-copy"></i>复制代码</span>
                  </div> -->
                </div>
              </div>
            </div>
          </div>
        </n-tab-pane>
      </n-tabs>
    </div>
    <div slot="footer" class="dialog-footer">
      <el-button @click="dialogVisible = false">关 闭</el-button>
    </div>
  </el-dialog>
</template>

<script>
import _ from 'lodash'
import Api from '@/api/application'
import { copyText, getImageUrl, loadCustomIconfont } from '@/utils'
import request from '@/request/request'
import { uploadPrefix } from '@/config/code-var'
export default {
  name: 'SourceManage',
  data() {
    return {
      dialogVisible: false,
      loading: false,
      sourceType: 'image',
      imageGroupList: [],
      imageList: [],
      defaultSelectId: 0,
      imageUploadAction: `${uploadPrefix}/${this.$route.query.id}/images`,
      iconfontUploadAction: `${uploadPrefix}/${this.$route.query.id}/iconfont`,
      headers: {
        'snc-token': JSON.parse(
          window.localStorage.getItem('loginKey') || '{}'
        )['snc-token'],
      },
      isShowManage: false,
      selectedImageList: [],
      iconfontData: null,
    }
  },
  watch: {
    sourceType(n) {
      if (n === 'image') {
        this.handleGetImageGroupList()
      } else {
        this.handleGetIconfont()
      }
    },
  },
  methods: {
    getImageUrl,
    open() {
      this.handleGetImageGroupList()
      this.dialogVisible = true
    },
    handleClose(done) {
      this.sourceType = 'image'
      this.loading = false
      this.dialogVisible = false
      done()
    },
    isSelected(image) {
      return this.defaultSelectId === image.id
    },
    handleSelect(image) {
      if (image) {
        this.imageList = image.images
        this.defaultSelectId = image.id
      }
    },
    handleGetImageGroupList(selectLastOne = false) {
      this.loading = true
      this.$http({
        url: Api.appSourceImageGroupList,
        data: {
          appId: +this.$route.query.id,
        },
      })
        .then((res) => {
          this.imageGroupList = res.data
          let selectedImage =
            this.imageGroupList.find(
              (item) => item.id === this.defaultSelectId
            ) || _.first(this.imageGroupList)
          if (selectLastOne === true) {
            selectedImage = _.last(this.imageGroupList)
          }
          this.handleSelect(selectedImage)
        })
        .finally(() => {
          this.loading = false
        })
    },
    async handleImageGroupRemoveById(id) {
      await this.$confirm('确认删除此分组吗？')
      this.loading = true
      this.$http({
        url: Api.appSourceImageGroupDelete,
        data: {
          id,
        },
      })
        .then((res) => {
          this.handleGetImageGroupList()
        })
        .finally(() => {
          this.loading = false
        })
    },
    handleImageGroup(command, image) {
      switch (command) {
        case 'delete': {
          this.handleImageGroupRemoveById(image.id)
          break
        }
        case 'edit': {
          this.$set(image, 'rename', true)
          break
        }
      }
    },
    handleRename(image, name) {
      this.loading = true
      this.$http({
        url: Api.appSourceImageGroupUpdate,
        data: {
          appId: +this.$route.query.id,
          id: image.id,
          name,
        },
      })
        .then((res) => {
          this.$set(image, 'rename', false)
          this.handleGetImageGroupList()
        })
        .finally(() => {
          this.loading = false
        })
    },
    handleAddGroup() {
      this.loading = true
      this.$http({
        url: Api.appSourceImageGroupSave,
        data: {
          appId: +this.$route.query.id,
          name: `新增分组${this.imageGroupList.length + 1}`,
        },
      })
        .then((res) => {
          this.handleGetImageGroupList(true)
        })
        .finally(() => {
          this.loading = false
        })
    },
    judgeFile(file) {
      const types = ['.jpg', '.png', '.jpeg', '.bmp']
      const isIn = types.some((type) => file.name.endsWith(type))
      if (!isIn) {
        this.$message.error('图片格式不合法')
        return false
      }
      // 大于1m
      if (file.size > 1 * 1024 * 1024) {
        this.$message.error('单张图片不能大于1m')
        return false
      }
      return true
    },
    judgeIconfontFile(file) {
      const types = ['.zip']
      const isIn = types.some((type) => file.name.endsWith(type))
      if (!isIn) {
        this.$message.error('必须为zip格式')
        return false
      }
      // 大于10m
      if (file.size > 10 * 1024 * 1024) {
        this.$message.error('zip包不能大于10m')
        return false
      }
      return true
    },
    handleUploadSuccess(res, file, fileList) {
      if (!fileList.some((f) => f.response?.msgCode !== 200)) {
        this.handleGetImageGroupList()
        this.$refs.imageUpload.clearFiles()
      }
    },
    handleSelectImage(img, e) {
      if (!this.isShowManage) {
        return
      }
      if (this.selectedImageList.some((item) => item.id === img.id)) {
        this.selectedImageList = this.selectedImageList.filter(
          (item) => item.id !== img.id
        )
      } else {
        this.selectedImageList.push(img)
      }
    },
    async handleDeleteBatch() {
      await this.$confirm('确认删除选择的图片吗？')
      this.loading = true
      this.$http({
        url: Api.appSourceImageBatchRemove,
        data: {
          ids: this.selectedImageList.map(item => item.id)
        }
      })
        .then((res) => {
          this.$message.success('操作成功！')
          this.handleGetImageGroupList()
          this.selectedImageList = []
          this.isShowManage = false
        })
        .finally(() => {
          this.loading = false
        })
    },
    async handleMoveToGroup(id) {
      const { name } = this.imageGroupList.find((item) => item.id === id)
      await this.$confirm(`确认将图片移动到分组[${name}]吗？`)
      this.loading = true
      this.$http({
        url: Api.appSourceImageBatchUpdate,
        data: this.selectedImageList.map(item => ({
          groupId: id,
          id: item.id
        }))
      })
        .then((res) => {
          this.$message.success('操作成功！')
          this.handleGetImageGroupList()
          this.selectedImageList = []
          this.isShowManage = false
        })
        .finally(() => {
          this.loading = false
        })
    },
    handleUploadIconfontSuccess(res) {
      if (res && res.msgCode === 200) {
        this.handleGetIconfont()
      }
    },
    handleBeforeUploadIconfont(file) {
      if (this.judgeIconfontFile(file)) {
        if (!this.iconfontData?.glyphs?.length) {
          return true
        } else {
          return this.$confirm('上传图标库将覆盖当前图标库，是否继续？')
        }
      } else {
        return false
      }
    },
    handleGetIconfont() {
      this.loading = true
      request({
        url: `${this.$store.state.CODE.lowCodeStore.appSourcePrefix}apps/${this.$route.query.id}/iconfont/iconfont.json`,
        method: 'GET',
        header: {
          'Content-Type': 'application/json',
        },
      })
        .then((res) => {
          this.iconfontData = res.data
          loadCustomIconfont()
        })
        .finally(() => {
          this.loading = false
        })
    },
    handleCopy(text) {
      copyText(text)
    },
    handleToggleManage() {
      this.isShowManage = !this.isShowManage
      this.selectedImageList = []
    },
    onExceed() {
      this.$message.error('每次最多上传20张图片！')
    }
  },
}
</script>

