<template>
  <el-dialog
    custom-class="low-code-dialog"
    class="create-app"
    :close-on-click-modal="false"
    :title="id == 0 ? '创建应用' : '修改应用'"
    :visible.sync="visible"
    @close="handleResetForm"
    width="800px"
    top="5vh"
    v-element-screen
  >
    <n-form
      class="create-form"
      ref="dataForm"
      :model="form"
      :rules="rules"
      @submit.native.prevent
      label-width="100px"
      v-loading="loading"
    >
      <div class="form-title">基础属性</div>
      <n-form-item label="应用名称" prop="name">
        <el-input
          class="form-input"
          v-model="form.name"
          placeholder="请输入应用名称"
        />
      </n-form-item>
      <n-form-item label="应用分类" prop="categoryCode">
        <app-classify
          v-model="form.categoryCode"
          placeholder="请选择应用分类"
        ></app-classify>
      </n-form-item>
      <n-form-item label="应用描述" prop="description">
        <el-input
          class="form-textarea"
          placeholder="请输入应用描述"
          v-model="form.description"
          type="textarea"
          :rows="4"
          maxlength="120"
          show-word-limit
        />
      </n-form-item>
      <n-form-item label="应用图标" prop="icon">
        <Upload class="app-icon" ref="upload" @fileChange="handleUploadFileChange" :autoUpload="false" :url="imageUrl || form.icon" @change="url => form.icon = url" />
        <div class="app-icon-desc">
          支持格式为jpg、png、jpeg、bmp，像素120*120，大小不超过1M
        </div>
        <recomment-img @select="$refs.upload.recieveFile($event)" />
      </n-form-item>
      <!-- <div class="form-title">应用分享</div>
      <n-form-item label="自定义访问地址" prop="sharingPath">
        <div class="form-href">
          <el-input class="href-after" :value="hrefPrefix" readonly />
          <el-input
            class="href-after"
            v-model="form.sharingPath"
            placeholder="请输入地址"
          />
          <el-button class="href-btn" type="primary" @click="copyText"
            >复制</el-button
          >
        </div>
      </n-form-item> -->
    </n-form>

    <div slot="footer" class="dialog-footer">
      <el-button
        :loading="loading"
        type="primary"
        @click="handleCreateAppSubmit"
        >确 认
      </el-button>
      <el-button :loading="loading" @click="close">取 消</el-button>
    </div>
  </el-dialog>
</template>

<script>
  //func
  import { copyText } from '@/utils'
  import { getValidators, required } from '@/utils/validateUtils'

  import API from '@/api/application'

  //components
  import AppClassify from '../appClassify'
  import recommentImg from '../recommentImg'
  import Upload from '@/components/Upload'
  import Select from '@/components/Select'

  export default {
    components: {
      AppClassify,
      Upload,
      Select,
      recommentImg,
    },
    props: {
      // 新建为0,编辑为应用id
      id: {
        type: Number,
        required: true,
      },
    },
    data() {
      return {
        visible: false,
        loading: false,
        form: {
          categoryCode: '',
          description: '',
          icon: '',
          name: '',
          sharingPath: '',
        },
        hrefPrefix: 'https://',
        rules: {
          name: getValidators([
            {
              message: '请输入应用名称',
              include: required,
              required: true,
              trigger: 'change',
            },
          ]),
          categoryCode: getValidators([
            {
              message: '请输入应用分类',
              include: required,
              required: true,
              trigger: 'change',
            },
          ]),
          // icon: getValidators([
          //   {
          //     message: '请选择应用图标',
          //     include: required,
          //     required: true,
          //     trigger: 'change',
          //   },
          // ]),
        },
        imageFile: null,
        imageUrl: ''
      }
    },
    mounted() {},
    methods: {
      getInfo() {
        this.loading = true
        this.$http({
          url: API.appInfoDetail,
          data: { id: this.id },
        })
          .then((res) => {
            const {
              categoryCode,
              description,
              sharingPath,
              name,
              icon,
            } = res.data
            this.form = {
              categoryCode,
              description,
              sharingPath,
              name,
              icon,
            }
          })
          .finally(() => {
            this.loading = false
          })
      },
      handleCreateAppSubmit() {
        this.$refs.dataForm.validate((valid) => {
          if (valid) {
            if (this.id) {
              this.handleUpdateApp()
            } else {
              this.handleCreateApp()
            }
          }
        })
      },
      handleCreateApp() {
        if (!this.imageFile) {
          this.$message.error('请选择应用图标')
          return
        }
        const params = this.form
        this.loading = true
        this.$http({
          url: API.appInfoAdd,
          data: params,
        })
          .then(async (res) => {
            try {
              const { data } = await this.handleUploadImage(res.data)
              if (data) {
                this.$message.success('应用创建成功！')
              }
            } catch(e) {
              this.$message.warn('应用创建成功，但图片上传失败！')
            }
            this.close()
            this.$emit('success')
          })
          .finally(() => {
            this.loading = false
          })
      },
      handleUpdateApp() {
        if (!this.form.icon && !this.imageFile) {
          this.$message.error('请选择应用图标')
          return
        }
        const params = {
          ...this.form,
          id: this.id,
        }
        this.loading = true
        this.$http({
          url: API.appInfoUpdate,
          data: params,
        })
          .then(async () => {
            if (this.imageFile) {
              try {
                const { data } = await this.handleUploadImage(this.id)
                if (data) {
                  this.close()
                  this.$emit('success')
                  this.$message.success('保存成功')
                }
              } catch(e) {
                this.close()
                this.$emit('success')
                this.$message.warn('应用图片保存失败')
              }
            } else {
              this.close()
              this.$emit('success')
              this.$message.success('保存成功')
            }
          })
          .finally(() => {
            this.loading = false
          })
      },
      handleUploadImage(id) {
        const formData = new FormData()
        formData.append('file', this.imageFile)
        formData.append('appId', id)
        return this.$http({
          url: API.appSourceIconUpload,
          headers: {
            'Content-Type': 'multipart/form-data',
          },
          customData: formData,
        })
      },
      handleResetForm() {
        this.$refs.dataForm.resetFields()
      },
      show() {
        if (this.id) {
          this.getInfo()
        }
        this.visible = true
      },
      close() {
        this.imageFile = null
        this.imageUrl = ''
        this.visible = false
      },
      copyText() {
        copyText(this.hrefPrefix + this.form.sharingPath)
      },
      handleUploadFileChange(file) {
        this.imageFile = file
        this.imageUrl = file ? URL.createObjectURL(file) : file
      }
    },

    computed: {},

    watch: {},
  }
</script>
<style lang="less" scoped>
  .create-app {
    /deep/ .el-dialog {
    }
    .create-form {
      .form-title {
        height: 16px;
        font-size: 14px;
        color: #333333;
        letter-spacing: 0;
        line-height: 16px;
        font-weight: 700;
        padding-left: 10px;
        margin-bottom: 20px;
        border-left: 2px solid #3f92fe;
      }
      .form-input {
        width: 200px;
      }
      .form-textarea {
        width: 500px;
      }
      .app-icon-desc {
        width: 595px;
        font-size: 12px;
        color: #999999;
        letter-spacing: 0;
        font-weight: 400;
        line-height: 1.5;
        margin-bottom: 7px;
      }
      .app-thumb {
        margin-bottom: 5px;
      }
      .form-href {
        display: flex;
        align-items: center;
        justify-content: flex-start;
        .href-after {
          width: 206px;
          margin-left: 6px;
        }
        .href-btn {
          margin-left: 6px;
        }
      }
    }
  }
</style>
