<template>
  <el-select
    :value="value"
    :placeholder="placeholder"
    :filterable="filterable"
    :disabled="disabled"
    @change="$emit('change', $event)"
    :clearable="clearable"
    v-bind="$props"
  >
    <el-option
      v-for="option in options"
      :label="option[labelKey]"
      :value="option[valueKey]"
      :key="option[valueKey]"
      :selected="option.selected"
      :disabled="option.disabled"
    ></el-option>
  </el-select>
</template>
<script>
  export default {
    model: {
      prop: 'value',
      event: 'change',
    },
    props: {
      // 下拉框选项
      options: {
        type: Array,
        default: () => [],
      },
      value: {
        type: [String, Number],
        default: '',
      },
      placeholder: {
        type: String,
        default: '',
      },
      // 是否可搜索
      filterable: {
        type: Boolean,
        default: false,
      },
      disabled: {
        type: Boolean,
        default: false,
      },
      clearable: {
        type: Boolean,
        default: true,
      },
      // option中 option中label的key
      labelKey: {
        type: String,
        default: 'label',
      },
      //  option中value的key
      valueKey: {
        type: String,
        default: 'value',
      },
      size: {
        type: String
      }
    },
    methods: {},
  }
</script>
