<template>
  <div>
    <error-log
      :logs="logsTree"
      :top="top"
      :logTotal="{ info: infoNum, error: errorNum }"
      @clear="clearLog"
    ></error-log>
  </div>
</template>

<script>
import _ from 'lodash'
import EventBus from '@/renderer/tools/eventBus'
import errorLog from './components/errorLog'
import { UPDATE_LOGS } from '@/config/eventKey'
import trigger from '@/renderer/tools/trigger'
import treeTool from 'tree-tool'
import { debounce } from '@/utils'
const mapLogs = treeTool.createInstance({
  id: 'id',
  children: 'children',
  pid: 'pid',
})

export default {
  components: {
    errorLog,
  },
  provide() {
    return {
      updatePanel: (val) => {
        this.$emit('update-panel', val)
      },
    }
  },
  props: {
    top: {
      type: Boolean,
      default: false,
    },
  },
  data() {
    return {
      logsTree: [],
      logsList: [],
      infoNum: 0,
      errorNum: 0,
      // 最大记录长度
      limit: 30
    }
  },
  mounted() {
    // 收集组件未加载时就有的日志
    trigger.logBuffer.forEach((item) => {
      this.updateLogs(item)
    })
    trigger.clearLogBuffer()
    EventBus.$on(UPDATE_LOGS, this.updateLogs)
  },
  beforeDestroy() {
    EventBus.$off(UPDATE_LOGS)
  },
  methods: {
    // 更新logs信息
    updateLogs(data, callback) {
      // 告诉emit方已收到
      callback && callback()

      this.updateLogList(data)
    },
    getErrorNumber() {
      //构造两层结构开始
      // 总信息数量
      let infoNum = 0
      // 总错误数量
      let errorNum = 0
      this.logsTree.forEach((item) => {
        item.errorNum = 0
        item.infoNum = 0
        item.desc = false

        mapLogs.forEach(item.children, (log) => {
          // 计算错误数量
          if (log.errorText) {
            item.errorNum++
          } else {
            item.infoNum++
          }
        })
        // 只统计一级大类的数量 || 改类型就是错误而不是日志
        if (item.errorNum > 0 || item.type === 'error') {
          errorNum++
        } else {
          infoNum++
        }
      })
      this.infoNum = infoNum
      this.errorNum = errorNum
    },
    updateLogList(logItem) {
      const { logsList } = this
      // 自身是不插入的, 只把子节点都指向当前的pid
      logItem.children = logItem.children || []
      if (logItem.pid) {
        logItem.children = logItem.children.map((item) => ({
          ...item,
          pid: logItem.pid,
          children: [],
        }))
      } else {
        logItem.children = logItem.children.map((item) => ({
          ...item,
          pid: logItem.id,
          children: [],
        }))
        logsList.push({ ...logItem, children: [], pid: '' })
      }

      logItem.children.forEach((item) => {
        logsList.push(item)
      })

      this.updateLogTree(logsList)
    },
    // 防抖处理,避免计算过多
    updateLogTree: debounce(function(logsList) {
      // 获取到树状结构
      this.logsTree = mapLogs.fromList(_.cloneDeep(logsList))
      // 不能无限增加, 超出长度, 把第一个删掉
      if (this.logsTree.length > this.limit) {
        this.logsTree = this.logsTree.slice(-this.limit)
        const list = []
        mapLogs.forEach(this.logsTree, item => {
          list.push({ ...item, children: [] })
        })
        this.logsList = _.cloneDeep(list)
      }
      this.getErrorNumber()
    }),
    clearLog() {
      this.logsTree = []
      this.logsList = []
      this.infoNum = 0
      this.errorNum = 0
    },
  },

  computed: {},

  watch: {},
}
</script>
