<template>
  <div>
    <el-popover ref="popover" placement="top-start" trigger="click">
      <div class="recomment-img">
        <img
          v-for="(img, index) in options"
          :key="index"
          :src="img"
          alt=""
          @click="imgClick(img)"
          ref="img"
        />
      </div>
    </el-popover>
    <el-button v-popover:popover>推荐图标</el-button>
  </div>
</template>

<script>
  import { base64ToFile, imageToBase64 } from '@/utils/file'
  export default {
    components: {},
    props: {
      options: {
        type: Array,
        default: () => {
          return Array(5).fill(0).map((item, index) => require(`./img/${index}.png`))
        }
      }
    },
    data() {
      return {}
    },
    created() {},
    mounted() {},
    methods: {
      imgClick(url) {
        imageToBase64(url).then((res) => {
          const file = base64ToFile(res, Date.now() + '.png')
          this.$emit('select', file)
          this.$refs.popover.doClose()
        })
      },
    },

    computed: {},

    watch: {},
  }
</script>
<style lang="less" scoped>
  .recomment-img {
    display: flex;
    img {
      cursor: pointer;
      width: 50px;
      height: 50px;
      margin-right: 10px;
      margin-bottom: 10px;
    }
  }
</style>
