<template>
  <Select
    :value="value"
    :options="list"
    labelKey="name"
    valueKey="code"
    :placeholder="placeholder"
    @change="$emit('change', $event)"
  />
</template>

<script>
  import * as API from '@/api/applicationMarket.js'
  import Select from '@/components/Select'
  export default {
    model: {
      prop: 'value',
      event: 'change',
    },
    components: {
      Select,
    },
    props: {
      value: {
        type: [String, Number],
        require: true,
      },
      placeholder: {
        type: String,
        defalut: '',
      },
    },
    data() {
      return {
        list: [],
      }
    },
    created() {
      this.getList()
    },
    mounted() {},
    methods: {
      // 获取分类列表
      getList() {
        API.appCategoryList().then((res) => {
          this.list = res.data
        })
      },
    },

    computed: {},

    watch: {},
  }
</script>
<style lang="less" scoped></style>
