<template>
  <section class="tag-wrap">
    <n-tooltip :value="desc" popper-class="top-popper">
      <el-tag class="comp-tag" size="mini">{{ name }}</el-tag>
    </n-tooltip>
    <div v-if="isShowPointer" class="go-debug" @click.stop="clickItem"></div>
  </section>
</template>

<script>
import { EDIT } from '@/config'
import { pageInject } from '@/renderer/tools/pageStore'
import { isEditMode } from '@/utils'
export default {
  inject: ['dndOptions', 'updatePanel', ...pageInject],
  components: {},
  props: {
    log: {
      type: Object,
    },
  },
  data() {
    return {
      action: {
        doSomething: this.doSomething,
      },
      panels: ['prop', 'style', 'event'],
    }
  },
  created() {},
  mounted() {},
  methods: {
    doSomething() {
      console.log(111)
    },
    async clickItem() {
      const {
        target: { elementId },
        callback,
        panel,
        clickType,
      } = this.log
      // 切换到那个组件
      if (this.dndOptions.selectedId !== elementId) {
        this.dndOptions.selectedId = elementId
        await this.$nextTick()
      }

      // 更新显示面板
      this.panels.includes(panel) && this.updatePanel(panel)
      await this.$nextTick()

      // 有回调就用回调, 没有就判断有没有要执行相应的方法
      if(typeof callback === 'function') {
        callback()
      } else {
        const action = this.action[clickType]
        typeof action === 'function' && action()
      }
    },
  },

  computed: {
    isShowPointer() {
      const {
        log: { target = {} },
      } = this
      return isEditMode() && target.elementId
    },
    name() {
      const { elementName, elementId, elementType } = this.log.target
      return elementType === 'page' ? '页面 - ' + this.$store.state.CODE.lowCodeStore.pageData.find(item => item.elementId === elementId)?.elementName : elementName
    },
    desc() {
      const { elementId, elementType, elementVersion } = this.log.target || {}
      if(!elementId) {
        return '未知组件'
      }
      return `组件id: (${elementId}) 组件名称: (${this.name}) 组件类型: (${elementType}) 组件版本: (${elementVersion})`
    },
  },

  watch: {},
}
</script>
<style lang='less' scoped>
.tag-wrap {
  display: inline-block;
  .el-tag.comp-tag {
    margin-left: 10px;
    cursor: auto;
    float: left;
  }

  .go-debug {
    float: left;
    margin-left: 10px;
    width: 25px;
    height: 25px;
    border-radius: 50%;
    cursor: pointer;
    background-size: 100% 100%;
    background-position: center;
    background-image: url('~@/components/errorCapture/components/compTag/img/right-arrow.png');
    &:hover {
      background-image: url('~@/components/errorCapture/components/compTag/img/right-arrow-active.png');
    }
  }
}
</style>